:root {
  /* 基本色 */
  --color-basic-white: #fafafa;
  --color-basic-lightGray: #e4e4e4;
  --color-basic-gray: #bebebe;
  --color-basic-darkGray: #505050;
  --color-basic-black: #1b1b1b;
  --color-basic-red: #ef4856;
  --color-basic-lightPurple: #c289ff;
  --color-basic-purple: #7519ff;
  --color-basic-darkPurple: #341496;
  --color-basic-skyblue: #00b2ff;

  /* 背景色 */
  --color-bg-base: #eee;
  --color-bg-primary: #484848;
  --color-bg-primaryLight: #7e7e7e;
  --color-bg-secondary: #a4a4a4;
  --color-bg-tertiary: #d8d8d8;
  --color-bg-tertiaryLight: #ededed;
  --color-bg-accent: #f00;
  --color-bg-accentLight: #ff7e7e;
  --color-bg-success: #00b2ff;
  --color-bg-successLight: #7dd6fc;
  --color-bg-info: #ffbd00;
  --color-bg-infoLight: #fcdb7d;
  --color-bg-warning: #ff7f00;
  --color-bg-warningLight: #fcbc7d;
  --color-bg-error: #f00;
  --color-bg-errorLight: #ff7e7e;
  /* テキスト色 */
  --color-text-default: #858585;
  --color-text-primary: #858585;
  --color-text-secondary: #8c8c8c;
  --color-text-light: #F5F5F5;
  --color-text-accent: #f00;
  --color-text-accentLight: #ff7e7e;
  --color-text-anchor: #00b2ff;
  --color-text-anchorLight: #7dd6fc;
  --color-text-positive: #00b2ff;
  --color-text-negative: #f00;
}
/* 色情報 */
:root {
  --color-base-black: #000;
  --color-base-codGray: #1b1b1b;
  --color-base-mineShaft: #222;
  --color-base-mercury: #e4e4e4;
  --color-base-dodgerBlue: #31abff;
  --color-base-puertoRico: #35caba;
  --color-base-amber: #ffbd00;
  --color-base-brilliantRose: #f9639c;
  --color-base-carnation: #ef4856;
  --color-base-concrete: #f2f2f2;
  --color-base-alabaster: #fafafa;
  --color-base-white: #fff;
}
/* ITCSS: Tools */
/* Default mixins and functions. */
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
:root {
  --animation-spin {
    animation-name: spin;
    animation-duration: 1s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }
}
/* レイアウト */
:root {
  /* 左揃え & 上揃え */
  --layout-start-start: {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
  }

  /* 天地中央配置 & 左揃え */
  --layout-center-start: {
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }

  /* 天地左右中央配置 */
  --layout-center-center: {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  /* 天地中央配置 & 右揃え */
  --layout-center-end: {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }

  /* 天地いっぱいまで広がる & 左右中央*/
  --layout-center-stretch: {
    display: flex;
    align-items: stretch;
    justify-content: center;
  }

  /* 左右に吸着 & 上揃え */
  --layout-between-start: {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  /* 左右に吸着 & 上下いっぱいまで広がる */
  --layout-between-stretch: {
    display: flex;
    align-items: stretch;
    justify-content: space-between;
  }

  /* 左右に吸着 & 天地中央*/
  --layout-between-center: {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  /* 左揃え & 上下いっぱいまで広がる*/
  --layout-stretch-start: {
    display: flex;
    align-items: stretch;
    justify-content: flex-start;
  }

  /* 縦方向*/
  /* 天地中央 & 左右いっぱいまで広がる */
  --layout-column-stretch-center: {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: center;
  }

  /* 各アイテムの周囲に均等なスペース & 左右いっぱいまで広がる */
  /* @see https://developer.mozilla.org/ja/docs/Web/CSS/justify-content */
  --layout-column-stretch-spaceEvenly: {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: space-evenly;
  }
}
/* テキスト */
:root {
  /* はみ出した部分を三点リーダー化 */
  /* `max-width`等と一緒に使用すること */
  --text-truncate: {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  /* overflow時にtext-breakさせる */
  --text-break: {
    word-break: break-all;
    word-wrap: break-word;
    overflow-wrap: break-word;
  }

  /* overflow時のtext-breakをキャンセルする */
  --text-break-cancel: {
    word-break: normal;
    word-wrap: normal;
    overflow-wrap: normal;
  }
}
/* ITCSS: Generic */
/* Ground-zero styles(Normalize.css, resets, box-sizing). */
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
a {
  text-decoration: none;
  outline: none;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
form {
  margin: 0;
}
input,
button,
textarea,
select {
  padding: 0;
  margin: 0;
  font-family: "Noto Sans", "Noto Sans CJK JP", "NotoSansCJKjp-Jxck", sans-serif;
  line-height: normal;
  background: none;
  border: none;
  border-radius: 0;
  outline: none;
  appearance: none;
}
/* Chromeのautofill機能で背景色がyellowに設定される問題への対応 */
@keyframes autofill {
  to {
    background: transparent;
  }
}
input:-webkit-autofill {
  animation-name: autofill;
  animation-fill-mode: both;
}
*,
*::before,
*::after {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
/* ITCSS: Base */
/* Unclassed HTML elements(type selectors). */
@font-face {
  font-family: "NotoSansCJKjp-Jxck";
  font-style: normal;
  font-weight: 300;
  src: url("font/NotoSansCJKjp-Jxck-Light.woff2") format("woff2"), url("font/NotoSansCJKjp-Jxck-Light.woff") format("woff");
}
@font-face {
  font-family: "NotoSansCJKjp-Jxck";
  font-style: normal;
  font-weight: 400;
  src: url("font/NotoSansCJKjp-Jxck-Regular.woff2") format("woff2"), url("font/NotoSansCJKjp-Jxck-Regular.woff") format("woff");
}
@font-face {
  font-family: "NotoSansCJKjp-Jxck";
  font-style: normal;
  font-weight: 700;
  src: url("font/NotoSansCJKjp-Jxck-Bold.woff2") format("woff2"), url("font/NotoSansCJKjp-Jxck-Bold.woff") format("woff");
}
html,
body {
  @apply --text-break;
  height: 100%;
  overflow-y: hidden;
  -ms-overflow-style: -ms-autohiding-scrollbar;
}
body {
  position: relative;
  min-width: 375px;
  font-family: "Noto Sans", "Noto Sans CJK JP", "NotoSansCJKjp-Jxck", sans-serif;
  font-size: 12px;
  line-height: 1.5;
  color: var(--color-base-mineShaft);
  background-color: var(--color-base-concrete);
}
/* ITCSS: Objects */
/* Cosmetic-free design patterns. */
.layer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  pointer-events: none;
}
.layer__content {
  pointer-events: auto;
}
/* @see: http://www.stubbornella.org/content/2010/06/25/the-media-object-saves-hundreds-of-lines-of-code/ */
.media {
  display: flex;
  align-items: flex-start;
}
.media__image {
  margin-right: 0;
}
.media__body {
  flex-grow: 1;
  width: 0;
  min-width: 0;
}
.HeaderNav {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  min-height: 50px;
  padding: 14px 15px;
  background-color: var(--color-bg-base);
}
.HeaderNav__menu {
  display: flex;
  align-items: center;
  margin-right: 20px;
}
.HeaderNav__title {
  font-size: 20px;
  font-weight: bold;
}
.Sidemenu {
  height: 100%;
  width: 320px;
  position: fixed;
  top: 0;
  left: 0;
  color: var(--color-text-light);
  transform: translate(-320px, 0);
  background: var(--color-bg-base);
  transition: transform .4s cubic-bezier(0.0, 0.0, 0.2, 1), width .4s cubic-bezier(0.0, 0.0, 0.2, 1);
}
.Application--menuopen .Sidemenu {
  transform: translate(0, 0);
  box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.4);
}
.Sidemenu__nav {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  min-height: 50px;
  padding: 14px 15px;
  background-color: var(--color-bg-base);
}
.Sidemenu__navMenu {
  display: flex;
}
.MainContainer {
  height: 100%;
  background: var(--color-bg-base);
}
.MainContainer__router {
  padding: 2em 8em;
}
@media (max-width: 767px) {
  .MainContainer__router {
    padding: 2em 4em;
  }
}
.GuideDrawer {
}
.ItemSection {

}
.ItemSection__title {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 20px;
}
.ItemSection__cards {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  overflow-x: scroll;
}
.ItemSection__cards > div {
  margin: 0 8px 8px 0;
}
.Hamburger,
.Hamburger span {
  display: inline-block;
  transition: all .4s;
  box-sizing: border-box;
  cursor: pointer;
}
.Hamburger {
  position: relative;
  width: 30px;
  height: 18px;
}
.Hamburger span {
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--color-text-default);
  border-radius: 4px;
}
.Hamburger span:nth-of-type(1) {
  top: 0;
}
.Hamburger span:nth-of-type(2) {
  top: 8px;
}
.Hamburger span:nth-of-type(3) {
  bottom: 0;
}
.Hamburger span:nth-of-type(2)::after {
  position: absolute;
  top: 0;
  left: 0;
  content: '';
  width: 100%;
  height: 2px;
  background-color: var(--color-text-default);
  border-radius: 4px;
  transition: all .4s;
}
.Application--menuopen .Hamburger span:nth-of-type(1) {
  transform: translateY(20px) scale(0);
}
.Application--menuopen .Hamburger span:nth-of-type(2) {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
.Application--menuopen .Hamburger span:nth-of-type(2)::after {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}
.Application--menuopen .Hamburger span:nth-of-type(3) {
  transform: translateY(-20px) scale(0);
}
.Cover {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0;
  pointer-events: none;
  background-color: black;
  cursor: pointer;
  transition: opacity 0.3s cubic-bezier(0.0, 0.0, 0.2, 1);
}
.Application--menuopen .Cover {
  pointer-events: auto;
  opacity: 0.4;
}
.RecordCard {
  display: block;
  width: 280px;
  background-color: red;
  padding: 10px;
}
.Application {
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
.Content {
  flex: 1;
  position: relative;
  overflow-x: hidden;
}
